
import { defineComponent, toRefs, watch, reactive, ref } from 'vue';

export default defineComponent({
    props:{
        bldgId: String
    },
    setup(props){
        //親コンポーネントから渡ってくるbldgIdを監視する
        const {bldgId} = toRefs(props);
        watch(bldgId, () =>{
            //alert("PopulationGraph.vue:bldgId=" + props.bldgId)
        });

        //dataの定義
        const state = reactive<{title: string}>({
            title: "PopulationGraph"
        });
        const text = ref<string>("HelloPopulationGraph");

        //methodの定義
        const showBldgId =():void =>{
            alert('PopGraph:建物ID：' + props.bldgId);
        };

        return{
            state,
            text,
            showBldgId
        }
    }
});
